/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /newurl/              ->  index
 * POST    /newurl/              ->  create
 * GET     /newurl//:id          ->  show
 * PUT     /newurl//:id          ->  update
 * DELETE  /newurl//:id          ->  destroy
 */

'use strict';

import _ from 'lodash';
import Newurl from './newurl.model';
import http from 'http';
import rp from 'request-promise';

function respondWithResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function(entity) {
    if (entity) {
			var finEnt = entity;
			finEnt.shortUrl = "https://littleurl.herokuapp.com/"+entity._id;
			entity._id = undefined;
      res.status(statusCode).json(entity);
    }
  };
}

function saveUpdates(updates) {
  return function(entity) {
    var updated = _.merge(entity, updates);
    return updated.save()
      .then(updated => {
        return updated;
      });
  };
}

function removeEntity(res) {
  return function(entity) {
    if (entity) {
      return entity.remove()
        .then(() => {
          res.status(204).end();
        });
    }
  };
}

function handleEntityNotFound(res) {
  return function(entity) {
    if (!entity) {
      res.status(404).end();
      return null;
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function(err) {
    res.status(statusCode).send(err);
  };
}

function handleRedirect(res, statusCode){
	statusCode = statusCode || 200;
	return function(entity) {
    if (entity) {
			res.redirect(entity.originalUrl);
    }
  };
}

function checkUrl(url){
	var urlReg = /(https?:\/\/(?:www\.|(?!www))[^\s\.]+\.[^\s]{2,}|www\.[^\s]+\.[^\s]{2,})/;
	var a = url;

	return urlReg.test(a);
}

function formatUrl(url){
	if (!/^(f|ht)tps?:\/\//i.test(url)) {
		url = "http://" + url;
	}
	return url;
}

// Gets a list of News
export function index(req, res) {
  return Newurl.find().exec()
    .then(respondWithResult(res))
    .catch(handleError(res));
}

export function redirectURL(req, res){
	var id = req.params.id;
	return Newurl.findById(id).exec()
	.then(handleEntityNotFound(res))
	.then(handleRedirect(res))
	.catch(handleError(res));
}
	
export function newURL(req, res) {
	var path = req.path.substr(8);
	if (checkUrl(path)){
		path = formatUrl(path);
		rp(path)
		.then(function(){
			Newurl.findURL(path, function(err, news){
				if (err){ return handleError(res, err);}
				if(news.length === 0) { 
					return Newurl.create({'originalUrl': path})
						.then(respondWithResult(res, 201))
						.catch(handleError(res));
				} else {
					var found = news[0];
					var id = found._id;
					found._id = undefined;
					found.shortUrl = "https://littleurl.herokuapp.com/"+id;

					return res.status(200).json(found);
				}
			})
		})
		.catch(function(err){
			handleError(err);
		})
	}
	else {
		return res.status(400).json({error: 'Not a valid URL'});
	}
}

// Creates a new New in the DB
export function create(req, res) {
  return Newurl.create(req.body)
    .then(respondWithResult(res, 201))
    .catch(handleError(res));
}

